.idle { 
  height: 32px;
  width: 32px;
  -webkit-animation: idle steps(4);
          animation: idle steps(4); 
  -webkit-animation-duration: 0.4s; 
          animation-duration: 0.4s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  background-image: url('../assets/idle_cat.png');
  -ms-interpolation-mode: nearest-neighbor;
      image-rendering: -webkit-optimize-contrast;
      image-rendering: -moz-crisp-edges;
      image-rendering: -o-pixelated;
      image-rendering: pixelated;
}

@-webkit-keyframes idle {
  from { background-position:0px 0px; }
  to { background-position:132px 0px; }
}

@keyframes idle {
  from { background-position:0px 0px; }
  to { background-position:132px 0px; }
}


.glow {
  -webkit-animation: glowing-effect;
          animation: glowing-effect;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes glowing-effect {
    0% {
        -webkit-filter: contrast(100%);
                filter: contrast(100%);
    }
    80% {
        -webkit-filter: contrast(150%);
                filter: contrast(150%);
    }
    100% {
        -webkit-filter: contrast(100%);
                filter: contrast(100%);
    }
}

@keyframes glowing-effect {
    0% {
        -webkit-filter: contrast(100%);
                filter: contrast(100%);
    }
    80% {
        -webkit-filter: contrast(150%);
                filter: contrast(150%);
    }
    100% {
        -webkit-filter: contrast(100%);
                filter: contrast(100%);
    }
}

.hurt {
  -webkit-animation: hurt;
          animation: hurt;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-transition-timing-function: ease;
       -o-transition-timing-function: ease;
          transition-timing-function: ease;
}

@-webkit-keyframes hurt {
    0% {
        background: #333;
    }
    40% {
        background: #d22;
    }
    60%, 100% {
        background: #333;
    }
}

@keyframes hurt {
    0% {
        background: #333;
    }
    40% {
        background: #d22;
    }
    60%, 100% {
        background: #333;
    }
}

 

.deadMap {
  -webkit-animation: dying;
          animation: dying;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}

@-webkit-keyframes dying {
    0% {
        -webkit-filter: grayscale(0%);
                filter: grayscale(0%);
    }
    100% {
        -webkit-filter: grayscale(100%);
                filter: grayscale(100%);
    }
}

@keyframes dying {
    0% {
        -webkit-filter: grayscale(0%);
                filter: grayscale(0%);
    }
    100% {
        -webkit-filter: grayscale(100%);
                filter: grayscale(100%);
    }
}



